*{
  font-family: Arial, Helvetica, sans-serif;
}
.container{
  background-color: #24232b;
  padding: 24px;
}

.header{
  color: white;
  display: flex;
  
  justify-content: space-between;
  font-size: 20px;
  font-weight: 700;
  padding-bottom: 20px;
}

button{
  padding: 10px;
  border-radius: 5px;
  background-color: #2a8b8b;
  border: none;
  color:white;
  text-transform: uppercase;
  font-weight: 700;
  cursor: pointer;
}

.copy-btn{
  height: 30px;
  font-size: 10px;
}

.char-length{
  display: flex;
  flex-direction: column;
  font-size: 20px;
  color: white;
  font-weight: 700;
  padding-bottom: 24px;
}
.char-length span{
  display: flex;
 
  justify-content: space-between;
  padding-bottom: 24px;
}
.checkbox{
  display: grid;
  grid-template-columns: auto auto;
  font-size: 16px;
  font-weight: 700;
  color:white;
  padding-bottom: 20px;
}
.checkbox div{
  display: flex;
  gap: 5px;
  padding-bottom: 25px;
}
.generate-btn{
  width: 100%;
  font-size: 20px;
  padding: 20px;
}

.error-message{
  color: red;
  padding: 5px;
}

.password-strength{
  width: 100%;
  display: flex;
  justify-content: space-between;
  color:white;
  font-size: 20px;
  padding: 5px;
}